@import url(https://use.typekit.net/aek6hsr.css);
body{margin:0;font-family:ocr-a-std,monospace;font-weight:400;font-style:normal;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#e5e5e5;color:#53565a;font-size:1.2rem}code{font-family:ocr-a-std,monospace}h1{font-size:2.2rem;font-weight:400;margin:0}h2{font-size:1.7rem;font-weight:400;margin:0}.muted{color:#b1b1b1}
.App .main{padding:50px;width:calc(100% - 100px )}.App .row-1{display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr;grid-gap:0px 50px;gap:0px 50px;margin-bottom:15vh}.App .row-1__time{grid-column-start:5}.App .row-2{margin-bottom:3vh}.App .row-3{display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr;grid-gap:0px 50px;gap:0px 50px;margin-bottom:15vh}.App .row-3__paragraph{grid-column-start:3;white-space:nowrap;text-transform:uppercase}.App .row-3__paragraph h2{margin-bottom:1.8vh}.App .row-3__paragraph span{color:#f24822}
.Background .Backdrop{position:fixed;width:100vw;height:100vh;overflow:hidden}.Background .Backdrop-grain{width:100vw;height:100vh;position:fixed;z-index:-1;opacity:.6;mix-blend-mode:overlay}.Background .Backdrop-overlay{position:fixed;width:100vw;height:100vh;opacity:.4;z-index:-2;background-color:#d8d9d7}.Background .Backdrop-blur{position:fixed;width:100vw;height:100vh;opacity:1;z-index:-3}.Background .Backdrop-circle{position:fixed;right:-5vw;bottom:-10vw;width:90vh;height:90vh;border-radius:50%;background-color:#f24822;-webkit-filter:blur(250px);filter:blur(250px);z-index:-4}
.Activelink{display:block;white-space:nowrap}.Activelink h2{color:#f24822;margin-top:4px}.Activelink-parent{display:flex;align-items:center;cursor:pointer}.Activelink-wrapper{display:inline-flex;align-items:center;border:#f24822 3px solid;padding:12px 20px}.Activelink-bar{width:130px;height:3px;background-color:#f24822;margin:auto 15px}.Activelink-arrow{margin-left:25px}
